*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.main_div{
  width: 100%;
  height: 100vh;
  background-color: cadetblue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div{
  width: 55%;
  height: 80vh;
  background-color: #953ad1;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

h1{
  color: rgb(22, 8, 8);
  background: transparent;
  background-color: violet;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid rgb(32, 17, 114);
  outline: none;
 
  
  
}

button{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: violet;
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}
button:hover{
  background-color: #20bf6b;
}
ol{
  margin-top: 20px;
}
ol li{
  padding-left: 5px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: violet;
  text-transform: capitalize;
  background-color: green;
  width: 50%;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;justify-content: center;
  align-items: center;
  background-color: violet;
  margin: 0 15px 0 35px;
  border-radius: 50%;
  color: aliceblue;
  box-shadow:5px 5px 25px -5px rgba(0, 0, 0, 0.3) ;
}
.todo_style .fa-times:hover{
  color: crimson;
}
.todo_style .fa-times:hover .list_style{
  color: crimson;
}
@media(max-width:700px){
  .center_div{
    width: 55%;
    
  }

  .todo_style .fa-times{
   
    display: flex;justify-content: center;
    align-items: center;
 
    margin: 0 15px 0 35px;
    
  }

  button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    
  }

}